<template>
  <div class="content_left">
    <div class="detail_blog" id="detail_blog">
      <h3 class="blog_title">{{ detailBlog.title }}</h3>
      <p
        class="blog_tags"
      >作者: 班铃钦 分类: {{detailBlog.tags}} 发布于: {{detailBlog.ctime}} 01:06 浏览({{detailBlog.views}}) 评论(13)</p>
      <div class="blog_content">{{ detailBlog.content }}</div>
    </div>
    <div class="blogComment">
      <ul>
        <li v-for="(key, index) of commentList" :key="index">
          <span>
            {{key.name}}：发表于 {{key.ctime}}
            <a href="#" @click="huifu(key.id)">[回复]</a>
          </span>
          <p>{{key.content}}</p>
        </li>
      </ul>
    </div>
    <Comment :blog_id="detailBlog.id"/>
  </div>
</template>

<script>
import api from "@/api/index.js";
import Comment from "@/components/Comment.vue"
export default {
  data() {
    return {
      detailBlog: {
        id: "1",
        title: "dsa",
        content: "dsa",
        views: "dsa",
        tags: "das",
        ctime: "dsa",
        utime: "dsa"
      },
      commentList:[],
      user: {
        user_name: "",
        email: "",
        comments: "",
        userText: "",
        text: "",
        svg: ""
      }
    };
  },
  components:{
    Comment
  },
  created() {
    var params = new URLSearchParams(location.search);
    var blog_id = params.get("blog_id");
    api.queryBlogById("?Id=" + blog_id).then(res => {
      var cDate = new Date(Number(res.data.data[0].ctime));
      var uDate = new Date(Number(res.data.data[0].utime));
      var tempObj = {
        id: res.data.data[0].id,
        title: res.data.data[0].title,
        content: res.data.data[0].content,
        views: res.data.data[0].views,
        tags: res.data.data[0].tags,
        ctime:
          "" +
          cDate.getFullYear() +
          "-" +
          (cDate.getMonth() + 1) +
          "-" +
          cDate.getDate(),
        utime:
          "" +
          uDate.getFullYear() +
          "-" +
          (uDate.getMonth() + 1) +
          "-" +
          uDate.getDate()
      };
      this.detailBlog = tempObj;
    });

  }
};
</script>

<style scoped>
/*
*
*   网页左边区域
*
*/
.container .content_left {
  width: 60%;
  display: inline-block;
}
.container .content_left .detail_blog {
  padding: 19px;
  margin-bottom: 20px;
  background-color: #f5f5f5;
  border: 1px solid #e3e3e3;
  border-radius: 4px;
  -webkit-box-shadow: 1px 1px 5px rgba(97, 97, 97, 0.48);
  -moz-box-shadow: 1px 1px 5px rgba(97, 97, 97, 0.48);
  box-shadow: 1px 1px 5px rgba(97, 97, 97, 0.48);
}
.content_left .detail_blog .blog_tags {
  margin: 20px 0;
  padding-bottom: 20px;
  border-bottom: 1px solid #b9b9b9;
}
.blogComment {
    width: 100%;
    margin: 20px 0px 0px 0px;
}

.blogComment ul {
    width: 98%;
}

.blogComment ul li {
    list-style: none;
    background: #d9edf7;
    border: 1px dashed #7ca4c1 ;
    margin: 10px 0px 0px 0px;
    padding: 10px 0px 10px 10px;
    border-radius: 5px;
}

.blogComment ul li span {
    /*background: pink;*/
    padding: 5px 0px 5px 10px;
}

.blogComment ul li p {
    margin: 20px 0px 0px 10px;
}

.comment {
  padding: 19px;
  margin-bottom: 20px;
  background-color: #f5f5f5;
  border: 1px solid #e3e3e3;
  border-radius: 4px;
  -webkit-box-shadow: 1px 1px 5px rgba(97, 97, 97, 0.48);
  -moz-box-shadow: 1px 1px 5px rgba(97, 97, 97, 0.48);
  box-shadow: 1px 1px 5px rgba(97, 97, 97, 0.48);
}
.comment .comment_title {
  font-weight: normal;
  font-size: 16px;
  margin-bottom: 10px;
}
.comment .comment_user {
  margin-bottom: 15px;
}
.comment .comment_user input,
.comment .comment_code input {
  display: inline-block;
  height: 18px;
  padding: 6px 12px;
  font-size: 14px;
  color: #555;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
}
.comment .comment_user input:last-of-type {
  width: 60%;
}
.comment .comment_content textarea {
  display: block;
  width: 90%;
  height: 80px;
  padding: 6px 12px;
  font-size: 14px;
  margin-bottom: 15px;
  line-height: 1.42857143;
  color: #555;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
}
.comment .comment_code {
  margin-bottom: 15px;
}
.comment button {
  cursor: pointer;
  padding: 8px 16px;
  color: #333;
  background-color: #fff;
  border: 1px solid #ccc;
}
.comment button:hover {
  background-color: #adadad;
}
</style>